<template>
  <div>
    <!-- <div
      ref="container"
      style="height: 300px; width: 100%"
      class="monaco-editor"
    ></div> -->
    <codemirror v-model="content" :options="options"></codemirror>
  </div>
</template>

<script>
import { codemirror } from 'vue-codemirror'
import '../lib/cm-setting'
export default {
  components: {
    codemirror,
  },
  props: {
    // 
    codes: {
      type: String,
    },
    language: {
      type: String,
      default: 'application/json',
    },
    readonly: {
      type: Boolean,
      default: function () {
        return false
      },
    },
  },
  watch: {
    codes: {
      immediate: true,
      handler(val) {
        if (val) {
          this.content = val
        }
      },
    },
    content: {
      handler(n) {
        this.$emit('change', n)
      },
    },
    readonly: {
      handler(val) {
        this.options.readOnly = val
      },
    },
  },
  data() {
    return {
      monacoEditor: null,
      readOnly: true,
      content: '',
      hoverProvider: null,
      options: {
        line: true,
        theme: 'lesser-dark', // 
        tabSize: 4, // 
        indentUnit: 2, //  2
        firstLineNumber: 1, //  1
        readOnly: this.readonly, // 
        autorefresh: true,
        smartIndent: true, // 
        lineNumbers: true, // 
        styleActiveLine: true, // 
        viewportMargin: Infinity, //
        showCursorWhenSelecting: true, // 
        mode: this.language,
      },
    }
  },
  methods: {},
  mounted() {},
}
</script>

<style scoped>
.monaco-editor {
  min-height: 400px;
  text-align: left;
}
</style>
