<template>
  <div>
    <el-form
      :model="bugForm"
      ref="bugForm"
      size="mini"
      label-width="120px"
      :rules="bugRule"
    >
      <el-form-item label="" prop="bugName">
        <el-input
          type="textarea"
          :autosize="{ minRows: 2, maxRows: 10 }"
          placeholder=""
          v-model="bugForm.bugName"
        ></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input
          v-model="bugForm.acceptor"
          placeholder=""
        ></el-input>
      </el-form-item>
      <el-form-item label="" v-if="edit">
        <el-select v-model="bugForm.status" placeholder="">
          <el-option
            v-for="(item, index) in statusList"
            :key="index"
            :label="item.statusName"
            :value="item.value"
          >
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="level">
        <el-select v-model="bugForm.level" placeholder="">
          <el-option label="P1" :value="1"></el-option>
          <el-option label="P2" :value="2"></el-option>
          <el-option label="P3" :value="3"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="environment">
        <el-input
          v-model="bugForm.environment"
          placeholder=""
        ></el-input>
      </el-form-item>
      <el-form-item label="bug" prop="scene">
        <el-input
          type="textarea"
          :autosize="{ minRows: 12, maxRows: 30 }"
          placeholder=""
          v-model="bugForm.scene"
        ></el-input>
      </el-form-item>
      <el-form-item label="" prop="demandId">
        <el-input
          placeholder="ID"
          v-model="bugForm.demandId"
        ></el-input>
      </el-form-item>
      <el-form-item label="" prop="replayStep">
        <el-input
          type="textarea"
          :autosize="{ minRows: 2, maxRows: 10 }"
          placeholder=""
          v-model="bugForm.bugContent"
        >
        </el-input>
      </el-form-item>
      <el-form-item label="" prop="expectResult">
        <el-input
          type="textarea"
          :autosize="{ minRows: 1, maxRows: 4 }"
          placeholder=""
          v-model="bugForm.expectResult"
        ></el-input>
      </el-form-item>
      <el-form-item label="" prop="realResult">
        <el-input
          type="textarea"
          :autosize="{ minRows: 1, maxRows: 4 }"
          placeholder=""
          v-model="bugForm.realResult"
        ></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-select
          v-model="bugForm.tags"
          filterable
          allow-create
          default-first-option
          placeholder=""
        >
          <el-option
            v-for="(item, index) in tagList"
            :key="index"
            :label="item.text"
            :value="item.value"
          >
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button size="mini" @click="closeBug"> </el-button>
        <el-button type="primary" @click="submitBug('bugForm')" size="mini"
          > </el-button
        >
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
import bugApi from '../../http/BugApi'
export default {
  props: {
    edit: Boolean,
    bug: String,
    iteration: {
      type: String,
      default: '',
    },
  },
  watch: {
    edit: {
      handler(val) {
        this.isEdit = val
      },
      deep: true,
      immediate: true,
    },
    bug: {
      handler(val) {
        this.bugId = val
        this.bugForm = {}
        this.getBug()
      },
      deep: true,
      immediate: true,
    },
    iteration: {
      handler(val) {
        this.iterationId = val
      },
      deep: true,
      immediate: true,
    },
  },
  data() {
    return {
      spaceId: '',
      iterationId: '',
      bugForm: {},
      statusList: [],
      isEdit: false,
      bugId: '',
      tagList: [
        { text: '', value: '' },
        { text: '', value: '' },
        { text: '', value: '' },
      ],
      bugRule: {
        bugName: [
          { required: true, message: '', trigger: 'blur' },
          { min: 10, message: '10', trigger: 'blur' },
        ],
        level: [
          { required: true, message: '', trigger: 'change' },
        ],
      },
    }
  },
  methods: {
    getBug() {
      if (this.edit) {
        bugApi.getBugDetail(this.bug).then((res) => {
          this.bugForm = res.data
          if (this.$refs.bugForm) {
            this.$refs.bugForm.resetFields()
          }
        })
      }
    },
    getstatusList() {
      bugApi.getBugStatuses().then((res) => {
        this.statusList = res.data
      })
    },
    closeBug() {
      this.bugForm = {}
      this.$emit('cancel')
    },
    submitBug(formName) {
      this.$refs[formName].validate((valid) => {
        if (!valid) {
          return false
        }

        this.bugForm.spaceId = this.spaceId
        this.bugForm.iterationId = this.iterationId
        if (this.edit) {
          bugApi.updateBug(this.bugForm).then((res) => {
            if (res.data) {
              this.$message.success('')
              this.closeBug()
            } else {
              this.$message.error('')
            }
          })
          return
        }
        bugApi.createBug(this.bugForm).then((res) => {
          if (res.data) {
            this.$message.success('')
            this.closeBug()
          } else {
            this.$message.error('')
          }
        })
      })
    },
  },
  created() {
    this.spaceId = this.$store.state.spaceId
    this.isEdit = this.edit
    this.getstatusList()
  },
}
</script>
